.login {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;

}

.news {
    padding: 20px;
    background: -webkit-linear-gradient(yellow, green);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 900;
}

.user {
    padding: 10px;
    margin-right: 30%;
}

.userlogin {
    padding: 10px;
    margin-right: 50%;
}

.loginbtn {
    margin-right: 30px;
    border: 2px solid black;
    border-radius: 10px;
}

.card {
    width: auto;
    height: auto;
    border: 2px solid black;
    border-radius: 10px;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 50px;
    box-shadow: 10px 10px lightblue;
}

.title {
    padding: 20px;
    color: rgb(255, 147, 47);
}
.newback{background-color: gray;}
.author {
    text-align: right;
    margin-right: inherit;
    color: lightgreen;
}
.navbar{display: flex;justify-content: flex-end;margin-bottom: 20px;background-color: brown;padding: 10px;}
button{margin-right: 20px;color:white;border: 2px solid black;background-color: transparent;padding: 5px;font-weight: 900;}
.description {
    padding-left: 50px;
    padding-right: 50px;
    padding-top:50px ;
    background-color: lightcyan;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.articles{overflow-y:'scroll';}
.url{ padding: 20px;margin-left: 0%;}
.description1 {
   padding-left: 50px;
   padding-right: 50px;
   padding-bottom: 50px;
    background-color: lightcyan;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.btn{display: flex;}
.detail{margin-bottom: 10px;margin-left: 10px;border-radius: 10px ;color:black;border: 2px solid black;background-color: transparent;padding: 5px;font-weight: 900;}
.detail1{display: flex;margin-top: 10px;margin-bottom: 10px;margin-left: 10px;border-radius: 10px ;color:black;border: 2px solid black;background-color: transparent;padding: 5px;font-weight: 900;}
